@media screen and (max-width: 600px) {
  .titleAbout {
    font-size: 5rem;
  }

  /* sparkle */
  /* .superscript::after {
    --size: 6rem;
  } */
}

@media screen and (max-width: 650px) {
  .heroText {
    justify-content: center;
    height: 285px;
  }
  .videoContainer {
    height: 350px;
    margin-top: 65px;
  }
  .videoContainer video {
    height: 350px;
    width: auto;
    object-fit: contain;
    margin-left: -370px;
  }
  .heroTitle {
    font-size: 35px;
  }

  .heroSubtitle {
    font-size: 20px;
  }

  .heroText {
    margin-right: auto;
  }
}
